import "./styles.css";

// initial todos
// DO NOT EDIT THIS ARRAY
// You may add props to objects if needed.
let todos = [
  {
    todoID: 0,
    todoText: "Finish Homework",
    todoComplete: false
  },
  {
    todoID: 1,
    todoText: "Walk the dog",
    todoComplete: true
  },
  {
    todoID: 2,
    todoText: "Clean my room",
    todoComplete: false
  }
];

let addTaskBtn = document.querySelector(".add-task-btn");
let addTask = document.querySelector(".add-task");
let todoListEle = document.querySelector(".todoList");

function appendTodo(todoText, todoID, todoComplete = false) {
  todoListEle.insertAdjacentHTML(
    "beforeend",
    `
  <li data-id="${todoID}" class="${todoComplete ? "done" : ""}">
    ${todoText}
    <span class="editBtn"><i class="fa fa-edit"></i></span>
    <span class="trashBtn"> <i class="fa fa-trash"></i></span>
  </li>
  `
  );
  let editBtn = document.querySelector(`li[data-id="${todoID}"] .editBtn`);
  let trashBtn = document.querySelector(`li[data-id="${todoID}"] .trashBtn`);

  editBtn.addEventListener("click", () => {
    let newText = prompt("Enter the new todo name");
    let currentTodo = todos.findIndex((todo) => todo.todoID === todoID);
    todos[currentTodo].todoText = newText;
    let todoEle = todoListEle.querySelector(`li[data-id="${currentTodo}"]`);
    todoEle.textContent = newText;
  });

  trashBtn.addEventListener("click", () => {
    let currentTodo = todos.findIndex((todo) => todo.todoID === todoID);
    let todoEle = todoListEle.querySelector(`li[data-id="${currentTodo}"]`);
    todoListEle.removeChild(todoEle);
    todos.splice(currentTodo, 1);
  });
  console.log("todos:", todos);
}

addTaskBtn.addEventListener("click", () => {
  let todoText = addTask.value;
  let todoID = todos.length;
  todos.push({ todoText, todoID, todoComplete: false });
  appendTodo(todoText, todoID);
});

for (let todo of todos) {
  appendTodo(todo.todoText, todo.todoID, todo.todoComplete);
}
